/* CREO LE VARIABILI E LE FUNZIONI SET PER CAMBIARNE IL VALORE */

export const ImageUpload = () => {
  const classes = useStyles();
  const [UserAlreadyExists,setUserAlreadyExists]=useState(false);
  const [UserNotFound,setUserNotFound]=useState(false);
  const [loggingIn,setloggingIn]=useState(false);
  const [loggedIn,setloggedIn]=useState(false);
  const [showMenu, setShowMenu] = useState(false);
  const [showWebcam, setShowWebcam] = useState(false);
  const [showWebcamData, setShowWebcamData] = useState();
  const [selectedFile, setSelectedFile] = useState();
  const [preview, setPreview] = useState();
  const [data, setData] = useState();
  const [image, setImage] = useState(false);
  const [generatedImage, setGeneratedImage] = useState(false);
  const [generatedImageUrl, setgeneratedImageUrl] = useState(false);
  const [isLoading, setIsloading] = useState(false);
/* CREO UN COMPONENTE PER IL BOTTONE PER TORNARE ALLA HOME */

function ClearButton({clearData}) {
  return (<ColorButton variant="contained" className={classes.clearButton} color="primary" component="span" size="large" onClick={clearData} startIcon={<Clear fontSize="large" />}>
    Clear
  </ColorButton>);
}
/* CREO UN COMPONENTE PER IL BOTTONE PER OTTENERE UNO SCREEN DALLA WEBCAM */

function ScreenshotButton({takeScreenShot}) {
  return (<ColorButton variant="contained" className={classes.clearButton} color="primary" component="span" size="large" onClick={takeScreenShot} startIcon={<Clear fontSize="large" />}>
   Take Photo
  </ColorButton>);
}
 /* CREO IL COMPONENTE WEBCAM E FACCIO LA CHIAMATA ALL'ENDPOINT /convert PER FARE UNO SCREENSHOT DELLA WEBCAM E OTTENRE LE PREDIZIONI DAI MODELLI */

  const CustomWebcam = () => {
    return (
      <div className="container">
        <Webcam ref={webcamRef}/>
      </div>
    );
  }
  const webcamRef = useRef(null);
  //LA FUNZIONE CAPTURE UTILIZZA IL COMPONENTE CustomWebcam PER OTTENERE UNO SCREENSHOT DALLA WEBCAM
  const capture = useCallback(async () => {
    try {
      if (!webcamRef.current) {
        throw new Error('Webcam reference is not initialized.');
      }
      const imageSrc = await webcamRef.current.getScreenshot(); //OTTENGO SCREENSHOT DALLA WEBCAM
      return imageSrc;
    } catch (error) {
      console.error('Error capturing image:', error);
      return null;
    }
  }, [webcamRef]);
  //CHIAMATA AL SERVER
  const takeScreenShot = async () => {
    CheckLogin();
    const imageSrc = await capture();
    if (imageSrc) {
        try {
          const base64Data = imageSrc.split(",")[1];//DATI IN FORMATO BASE64DATA DELL'IMMAGINE IN FORMATO webp
          const res = await axios({
            method: "post",
            url: "https://analisifacciamachinelearning.online/convert",
            data: {
              base64_data: base64Data 
            }
          });          
          if (res.status === 200) {
                setShowWebcamData(res.data)//SETTO IL PARAMETRO ShowWebcamData CON LE INFORMAZIONI {"predicted_gender": predicted_gender,  "predicted_race": predicted_race,"predicted_age": predicted_age} OTTENUTE DAL SERVER
            }
        } catch (error) {
            console.error("Error:", error);
        }
    } else {
        console.log("No image captured yet.");
    }
};
/*RESETTO TUTTI I PARAMETRI PER TORNARE AL MENU PRINCIPALE*/ 
  const clearData = () => {
    setloggingIn(false);
    setUserAlreadyExists(false);
    setUserNotFound(false);
    setData(null);
    setImage(false);
    setGeneratedImage(false);
    setShowMenu(false);
    setShowWebcam(false);
    setShowWebcamData(null);
    setSelectedFile(null);
    setPreview(null);
    setgeneratedImageUrl(null)
  };
    const registra = async(event) => {
      try {
        const data_Utente = {
          email:event["email"],
          password:event["password"],
      };
        const res = await axios({
          method: "post",
          url: "https://analisifacciamachinelearning.online/registra",
          data:data_Utente,
        });          
        if (res.status === 200) {
              console.log("Collegamento andato a buon fine")
              console.log(res.data)

              if(res.data!==0){
                setloggedIn(true)
                setloggingIn(false)
              }
              else{
                setUserAlreadyExists(true)

              }
          }
      } catch (error) {
          console.error("Error:", error);
      }
  
    };
  const Login = async(event) => {
    try {
   
     const data_Utente = {
      email:event["email"],
      password:event["password"],
  };
      const res = await axios({
        method: "post",
        url: "https://analisifacciamachinelearning.online/login",
        data:data_Utente,
      });          
      if (res.status === 200) {
            console.log("Collegamento andato a buon fine")
            if(res.data!==0){
              setloggedIn(true)
              setloggingIn(false)
            }
            else{
              
              setUserNotFound(true)
            }
        }
    } catch (error) {
        console.error("Error:", error);
    }

  };
  const HandleLoginClick=()=>{
   
    setloggingIn(true)
}
 const CheckLogin=()=>{
  if(!loggedIn){
    HandleLoginClick()
  }
}
 /* CREO IL COMPONENTE LoginForm PER IL LOGIN DELL'UTENTE*/ 

const LoginForm = () => {
  const classes = useStyles();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const handleLogin = () => {
     setUserAlreadyExists(false);
     setUserNotFound(false);
     Login({email, password})
  };

  const handleRegister = () => {
    setUserAlreadyExists(false);
    setUserNotFound(false);
    registra({email, password})
  };

  return (
    <div className={classes.formContainerInput}>
      <Typography variant="h5" className={classes.Form_TitleInput}>
        Login
      </Typography>

      <Grid container spacing={2}>
        <Grid item xs={12}>
          <FormControl className={classes.formControlInput}>
            <InputLabel htmlFor="email">Email</InputLabel>
            <Input
              id="email"
              type="email"
              value={email}
              onChange={(event) => setEmail(event.target.value)}
            />
          </FormControl>
        </Grid>

        <Grid item xs={12}>
          <FormControl className={classes.formControlInput}>
            <InputLabel htmlFor="password">Password</InputLabel>
            <Input
              id="password"
              type="password"
              value={password}
              onChange={(event) => setPassword(event.target.value)}
            />
          </FormControl>
        </Grid>
      </Grid>

      <Button
        variant="contained"
        color="primary"
        className={classes.buttonInput}
        onClick={handleLogin}
      >
        Login
      </Button>

      <Button
        variant="contained"
        color="secondary"
        className={classes.buttonInput}
        onClick={handleRegister}
      >
        Register
      </Button>
      {UserNotFound &&(
        <div style={{ color: 'red', marginTop: '10px' }}>
        User not found, try again.
      </div>
      
      )}
      {UserAlreadyExists &&(
        <div style={{ color: 'red', marginTop: '10px' }}>
        L'utente esiste già
      </div>
      
      )}
       <Button
        variant="contained"
        color="tertiary"
        className={classes.buttonInput}
        onClick={clearData}
      >
        Home
      </Button>
    </div>
  );
};
 /* CREO IL COMPONENTE MyDropdownMenu CON UN FORM CHE MANDA AL SERVE LE INFOMAZIONI RELATIVE ALL' IMMAGINE DA GENERARE*/ 
  const MyDropdownMenu = () => {
    const classes = useStyles();
    const [age, setAge] = useState(''); 
    const [gender, setGender] = useState('');
    const [Race, setRace] = useState('');
    //SE IL BOTTONE DEL FORM VIENE PREMUTO CHIAMO L'ENDPOINT /generate CON I DATI DEL FORM PER GENERARE UN IMMAGINO
    const handleButtonClick = () => {

      setGeneratedImage(true)
      handleChange({ age, gender, Race });
    };

    const handleChange = async (event) => {
      
        try {
          const dataToSend = {
              Race:event["Race"],
              age:event["age"],
              gender:event["gender"]
          };
          const res = await axios({
            method: "post",
            url: "https://analisifacciamachinelearning.online/generate",
            data:dataToSend 
          });          
          if (res.status === 200) {
                setgeneratedImageUrl(res.data.imageUrl)
            }
        } catch (error) {
            console.error("Error:", error);
        }
    
    };
    const toHome=()=>{
      clearData()
    }
    //CLASSI RELATIVE AI MODELLI
    const gender_names  = ['Female', 'Male']
    const race_class_names=["White", "Black", "Asian", "Indian", "Others (Hispanic, Latino, Middle Eastern)"]
    const age_class_names=["1 to 2","3 to 9","10 to 20","21 to 27","28 to 45","46 to 65","older than 65"]
    return (
      // CREAZIONE DELLA CARD CON AL SUO INTERNO IL FORM PER LA GENERAZIONE DELL'IMMAGINE 
      <React.Fragment>
          <div className={classes.Form_Title}>Select</div>
  
        <FormControl className={classes.formControl}>
          <InputLabel id="age-label">Age</InputLabel>
          <Select
            labelId="age-label"
            id="select-age"
            value={age}
            onChange={(event) => setAge(event.target.value)}
          >
            {age_class_names.map((ageRange, index) => (
              <MenuItem key={index} value={ageRange}>{ageRange}</MenuItem>
            ))}
          </Select>
        </FormControl>
  <br></br>
        <FormControl className={classes.formControl}>
          <InputLabel id="gender-label">Gender</InputLabel>
          <Select
            labelId="gender-label"
            id="select-gender"
            value={gender}
            onChange={(event) => setGender(event.target.value)}
          >
            {gender_names.map((gender, index) => (
              <MenuItem key={index} value={gender}>{gender}</MenuItem>
            ))}
          </Select>
        </FormControl>
        <br></br>
  
        <FormControl className={classes.formControl}>
          <InputLabel id="Race-label">Race</InputLabel>
          <Select
            labelId="Race-label"
            id="select-Race"
            value={Race}
            onChange={(event) => setRace(event.target.value)}
          >
            {race_class_names.map((Race, index) => (
              <MenuItem key={index} value={Race}>{Race}</MenuItem>
            ))}
          </Select>
        </FormControl>
        <br></br>
  
        <Button
          variant="contained"
          color="primary"
          className={classes.button}
          
          onClick={handleButtonClick}
        >
          Apply
        </Button>
        <br></br>
        <Button
          variant="contained"
          color="secondary"
          className={classes.button}
          onClick={toHome}
        >
          Go back
        </Button>
      </React.Fragment>
    );
  };

/*FUNZIONE PER L'UPLOAD DI UN IMMAGINE E LA CHIAMATA ALL'ENDPOINT PREDICT*/ 
  useEffect(() => {
    if (!selectedFile) {
      setPreview(undefined);
      return;
    }
    const objectUrl = URL.createObjectURL(selectedFile);
    setPreview(objectUrl);
  }, [selectedFile]);
  useEffect(() => {
    const sendFile = async () => {
      if (image) {
        let formData = new FormData();
        formData.append("file", selectedFile);
        let res = await axios({
          method: "post",
          url: "https://analisifacciamachinelearning.online/predict",
          data: formData,
        });
        if (res.status === 200) {
          setData(res.data);
          console.log(res.data)
        }
        setIsloading(false);
      }
    };
  
    if (!preview) {
      return;
    }
    setIsloading(true);
    sendFile();
  }, [preview,image,selectedFile]);
  /*FUNZIONE CHIAMATA AL MOMENTO DELLA PRESSIONE DEL BOTTONE DI "UPLOAD IMAGE"*/
  const onSelectFile = (files) => {
    if (!files || files.length === 0) {//SE IL FILE NON ESISTE LE VARIABILI VENGONO MESSE IN UNO STATO CHE PREAVVISA IL FALLIMENTO DELL'UPLOAD
      setSelectedFile(undefined);
      setImage(false);
      setData(undefined);
      return;
    }
    setSelectedFile(files[0]);//SE HA SUCCESSO METTE L'IMMAGINE IN SelectedFile E SETTA IMAGE=true E INIZIALIZZA DATA SENZA DARGLI UN VALORE
    setData(undefined);
    setImage(true);
    CheckLogin();

  };
  const Generate_Face = () => {
    CheckLogin();

    setShowMenu(true);
  };
  const ShowWebcam = () => {
    CheckLogin();

    setShowWebcam(true);
  };
 

  return (
    /* INIZIO PAGINA */
    
    <React.Fragment>

      {/*BARRA DI INTESTAZIONE   */}
      <AppBar position="static" className={classes.appbar} style={{ display: "flex", justifyContent: "center", alignItems: "center", textAlign: "center", color: "white", backgroundColor: "#26363D" }}>
  <Toolbar style={{ width: "100%" }}>
    <Typography className={classes.title} variant="h6" noWrap style={{ fontSize: "1.5rem", fontWeight: "bold" }}>
      Face attributes recognition
    </Typography>

    <div className={classes.grow} style={{ flexGrow: 1 } } />
   {!loggedIn && (
      <Button variant="contained" className={classes.loginButton} onClick={HandleLoginClick}>
          Log In
      </Button>)}
   
  </Toolbar>
</AppBar>

      {/* PARTE PRINCIPALE DEL SITO */}
      <Container maxWidth={false} maxHeight={false} className={classes.mainContainer} disableGutters={false}>
      <Grid
  className={classes.gridContainer}
  container
  direction="row"
  justifyContent="center"
  alignItems="center" 
  spacing={2}
  style={{backgroundColor:"transparent"}}
>
      {image && !showWebcam && (
        <Grid item xs={12} md={5}> 
            <Card className={`${classes.imageCard} ${!image ? classes.imageCardEmpty : ''}`}style={{ height: "auto",backgroundColor:"transparent"}}>

        <CardActionArea >
          <CardMedia
            className={classes.media}
            image={preview}
            component="image"
            title="Contemplative Reptile"
          />
        </CardActionArea>
        </Card>
      </Grid>
      )}
      {/* PAGINA PER NON SI STA PROVANDO IL LOG IN*/}
      {!loggingIn &&( 
        <>
        
      {/* PAGINA PER SCREENSHOT DI WEBCAM PRE CHIAMATA AL SERVER*/}
      {showWebcam && !showWebcamData &&(
  <Grid
  className={classes.gridContainer}
  container
  direction="row"
  justifyContent="center"
  alignItems="center" 
  spacing={2}
>    
    <Grid item xs={6} md={12}> 
      <Card className={`${classes.webcamCard} ${!image ? classes.imageCardEmpty : ''}`} style={{marginBottom:"20px",  backgroundColor: "white" }}>
          <CustomWebcam/>
      </Card>
    </Grid>
    
    <Grid item xs={12} md={6} className={classes.buttonGrid}>
    <ScreenshotButton   takeScreenShot={takeScreenShot}  />

    </Grid>
    <Grid item xs={12} md={6} className={classes.buttonGrid} >
    <ClearButton   clearData={clearData}  />
    </Grid>
        </Grid>  )}    
        {/* PAGINA PER SCREENSHOT DI WEBCAM POST CHIAMATA AL SERVER*/}
        {showWebcam && showWebcamData &&(
<Grid
  className={classes.gridContainer}
  container
  direction="row"
  justifyContent="center"
  alignItems="center" 
  spacing={2}
>    
    <Grid item xs={6} md={6}> 
      <Card className={`${classes.webcamCard} ${!image ? classes.imageCardEmpty : ''}`} style={{marginBottom:"20px",  backgroundColor: "white" }}>
          <CustomWebcam/>
      </Card>
    </Grid>
        {/*CARD DESTINATA AI RISULTATI OTTENUTI DALLA CHIAMATA AL SERVER DALL'IMMAGINE DELLA WEBCAM*/}
    <Grid item xs={6} md={6}> 
      <Card className={`${classes.imageCard} ${classes.detail}`}>
        <CardContent>
          <TableContainer component={Paper} className={classes.tableContainer}>
            <Table className={classes.table} size="small" aria-label="simple table">
              <TableHead className={classes.tableHead}></TableHead>
              <TableBody className={classes.tableBody}>
                <TableRow className={classes.tableRow}>
                  <TableCell component="th" scope="row" className={classes.tableCell}>
                    {showWebcamData.predicted_gender}
                  </TableCell>
  
                </TableRow>
                <TableRow className={classes.tableRow}>
                  <TableCell component="th" scope="row" className={classes.tableCell}>
                    {showWebcamData.predicted_race}
                  </TableCell>
            
                </TableRow>
                <TableRow className={classes.tableRow}>
                  <TableCell component="th" scope="row" className={classes.tableCell}>
                    {showWebcamData.predicted_age}
                  </TableCell>
                </TableRow>
              </TableBody>
            </Table>
          </TableContainer>
        </CardContent>
      </Card>
        </Grid>
    <Grid item xs={12} md={6} className={classes.buttonGrid}>
      <ScreenshotButton   takeScreenShot={takeScreenShot}  />
    </Grid>
    <Grid item xs={12} md={6} className={classes.buttonGrid} >
    <ClearButton   clearData={clearData}  />
    </Grid>
        </Grid>
)}
    {/*PAGINA MENU PRINCIPALE PRIMA DI PREMERE BOTTONI */}
      {!image && !showMenu && !showWebcam &&(
  <Grid
    className={classes.gridContainer}
    container
    direction="row"
    justifyContent="center"
    alignItems="center" 
    spacing={2}
  >
    <Grid item xs={12} md={4}> 
      <Card className={`${classes.imageCard2} ${classes.detail2}`}>
        <CardContent className={classes.content} style={{backgroundColor:"#26363D", width: "100%",height:"100%",overflow:"hidden",padding:"0"}}>
          <DropzoneArea
            acceptedFiles={['image/*']}
            dropzoneText={<div style={{ color:'white'}}>Drag the image of a face</div>}
            onChange={onSelectFile} // CHIAMO LA FUNZIONE PER L'INSERIMENTO DEL FILE DAL FILE SYSTEM
            classes={{ root: classes.dropzoneArea }}
          />
        </CardContent>
      </Card>
    </Grid>
    <Grid item xs={12} md={4}> 
      <Card className={`${classes.imageCard2} ${classes.detail2}`}>
        <CardContent className={classes.content} style={{backgroundColor:"#26363D", width: "100%",height:"100%",overflow:"hidden",padding:"0"}} onClick={ShowWebcam}>
          <div className={classes.dropzoneTextStyle}>Webcam</div>
        </CardContent>
        </Card>
        </Grid>

        <Grid item xs={12} md={4}> 
        <Card className={`${classes.imageCard2} ${classes.detail2}`}>

        <CardContent className={classes.content} style={{backgroundColor:"#26363D", width: "100%",height:"100%",overflow:"hidden",padding:"0"}} onClick={Generate_Face}>
          <div className={classes.dropzoneTextStyle}>Generate Face</div>
        </CardContent>
        </Card>
        
        </Grid>
        

  </Grid>
  
)}
{/*COMPONENTI PER LA PARTE DI GENERAZIONE IMMAGINI */}


        {/*COMPONENTE PER PAGINA POST RICHIESTA AVVENUTA CON SUCCESSO */}
        {generatedImage && generatedImageUrl && (
            <Grid item xs={12} md={6}>
              <Card className={classes.imageCard}>
                <CardMedia
                  component="img"
                  alt="Generated Image"
                  height="500"
                  image={generatedImageUrl}
                  title="Generated Image"
                />
              </Card>
            </Grid>
          )}
        {/*COMPONENTE PER PAGINA RICHIESTA IN CORSO*/}

        {generatedImage && !generatedImageUrl && (
            <Grid item xs={12} md={6}>
            <Card className={`${classes.imageCard} ${classes.detail}`}>
            <CardContent className={classes.loader}>
            <CircularProgress color="secondary" />
            <Typography variant="h6" noWrap>
              Processing
            </Typography>
            </CardContent>
            </Card>
            </Grid>
          )}
        {/*COMPONENTE PER PAGINA PRE RICHIESTA (UTILIZZA IL COMPONENTE MyDropDownMenu DEFINITO IN PRECEDENZA*/}
        {showMenu && !showWebcam &&(
            <Grid item xs={12} md={4}> 
          <Card className={`${classes.imageCard} ${classes.menuCard}`} style={{backgroundColor:"white"}}>
              <CardContent>
              <MyDropdownMenu>              
              </MyDropdownMenu>

              </CardContent>
            </Card>
            </Grid>

          )}

{/*CARD DESTINATA AI RISULTATI OTTENUTI DALLA CHIAMATA AL SERVER DALL' UPLOAD DELL'IMMAGINE*/}

{!showWebcam &&(
  <Grid item xs={12} md={5}> 
    {data && (
      <Card className={`${classes.imageCard} ${classes.detail}`}>
        <CardContent>
          <TableContainer component={Paper} className={classes.tableContainer}>
            <Table className={classes.table} size="small" aria-label="simple table">
              <TableHead className={classes.tableHead}></TableHead>
              <TableBody className={classes.tableBody}>
                <TableRow className={classes.tableRow}>
                  <TableCell component="th" scope="row" className={classes.tableCell}>
                    {data.predicted_gender}
                  </TableCell>
                
                </TableRow>
                <TableRow className={classes.tableRow}>
                  <TableCell component="th" scope="row" className={classes.tableCell}>
                    {data.predicted_race}
                  </TableCell>
    
                </TableRow>
                <TableRow className={classes.tableRow}>
                  <TableCell component="th" scope="row" className={classes.tableCell}>
                    {data.predicted_age}
                  </TableCell>
                </TableRow>
              </TableBody>
            </Table>
          </TableContainer>
        </CardContent>
      </Card>
    )}
    {isLoading && (
      <Card className={`${classes.imageCard} ${classes.detail}`}>
        <CardContent className={classes.loader}>
          <CircularProgress color="secondary" />
          <Typography variant="h6" noWrap>
            Processing
          </Typography>
        </CardContent>
      </Card>
    )}
  </Grid>
)}
  {data && !showWebcam &&(
    <Grid  item xs={12} md={12} className={classes.buttonGrid} span={2}>
      <ClearButton   clearData={clearData}  />
    </Grid>
  )}
  </>
)}
{loggingIn && (
  <React.Fragment > 
        <Grid item xs={12} md={4}> 
              <CardContent>
              <LoginForm/>
              </CardContent>
            </Grid>
        </React.Fragment >
)}
</Grid>


      </Container >
    </React.Fragment >
  );
};